
.para {
  position: absolute;
  top: 10%;
  left: 10%;
  text-align: center;
}
h2 {
  font-size: 100px;
  opacity: 40%;
}

.para h3 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 40px;
  opacity: 60%;
}
.para p {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 24px;
  margin-top: 20px;
}

.notfoundHomePage {
  opacity: 70%;
  font-size: 15px;
  background-color: black;
  color: white;
  padding: 14px;
  margin-top: 5%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  border: none;
  transition: 0.5s;
  border: 1px solid;
  border-radius: 8px;
  width: 200px;
}
.notfoundHomePage:hover {
  cursor: pointer;
  border: 1px solid;
  scale: 1.1;
  background-color: forestgreen;
}
